.App {
    overflow: hidden;
}

.background {
    background: url('./8d6db2b6a2aeebb41b20ab812c2ce0c8.jpg');
    background-attachment: fixed;
    min-height: 100vh;
    width: 100vw;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.glass {
    cursor: pointer;
    position: relative;
    margin: 0;
    padding: 2rem;
    width: 100vw;
    height: 100vh;
    border-radius: 0;
    background-color: rgba(255, 255, 255, .11);
    backdrop-filter: blur(12px);
    box-shadow: 0 1rem 1rem -1rem rgba(0, 0, 0, 0.23);
    overflow: hidden;
    transform-origin: center;
    transition: transform 0.1s ease-in-out;
    color: white;
}

.glass p {
    font-size: 1.5rem;
    text-align: center;
    margin-top: 6rem;
}

.glass.small {
    height: 50vh;
}

.glass::after,
.glass::before {
    position: absolute;
    content: "";
    width: 10rem;
    height: 2000px;
    transform: rotate(45deg);
    bottom: -800px;
    right: 0;
    border: 10rem solid rgba(255, 255, 255, .05);
    backdrop-filter: blur(24px);
    opacity: 0.1;
}
.glass::after {
    top: -800px;
    left: 0;
    right: unset;
    bottom: unset;
}

.glass .hint .closed {
    display: block;
}
.glass .hint .open {
    display: none;
}

.glass.holding {
    transform: translateY(-0.5rem);
    box-shadow: 0 1.1rem 1rem -1rem rgba(0, 0, 0, 0.23);
}

.glass.go-up {
    transform: translateY(-500%);
    transition: transform 0.4s ease-in-out;
}

.glass.open .hint .open {
    display: block;
}

.glass.open .hint .closed {
    display: none;
}

.glass.go-up .avatar,
.glass.go-up .hint {
    display: none;
}

.hint {
    position: absolute;
    z-index: 9999;
    bottom: 8rem;
    left: 50%;
    transform: translateX(-50%);
    color: white;
    font-size: 1.5rem;
}

@media(min-width: 768px) {
    .background {
        flex-direction: row;
    }
    .glass {
        border-radius: 2rem;
        width: calc(calc(100vh - 4rem) * 0.5625);
        height: calc(100vh - 4rem);
        margin: 1rem;
    }
    .glass.small {
        width: calc(calc(100vh - 4rem) * 0.5625);
        height: calc(50vh - 4rem);
    }
    .page > div {
        display: flex;
    }
}

.hint img {
    height: 32px;
    width: 32px;
    margin: 32px auto;
    display: block;
}

.hint .open {
    position: relative;
    left: 3px;
    width: 34px;
}

h1 {
    text-align: center;
    color: white;
    margin: 0;
    font-weight: normal;
}

.image {
    width: 128px;
    height: 128px;
    background-color: blue;
    border-radius: 50%;
    margin: 0 auto;
    overflow: hidden;
    margin-top: 8rem;
    margin-bottom: 2rem;
}

.image img {
    width: 128px;
    height: 128px;
}

.avatar {
    position: relative;
    width: 256px;
    margin: 0 auto;
}

@keyframes appear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes from-bottom {
    0% {
        transform: translateY(500%);
    }
    50% {
        transform: translateY(-1rem);
    }
    75% {
        transform: translateY(1rem);
    }
    100% {
        transform: translateY(0);
    }
}

@keyframes from-top {
    0% {
        transform: translateY(-500%);
    }
    50% {
        transform: translateY(1rem);
    }
    75% {
        transform: translateY(-1rem);
    }
    100% {
        transform: translateY(0);
    }
}

@keyframes from-left {
    0% {
        transform: translateX(-500%);
    }
    50% {
        transform: translateX(1rem);
    }
    75% {
        transform: translateX(-1rem);
    }
    100% {
        transform: translateX(0);
    }
}

@keyframes from-right {
    0% {
        transform: translateX(500%);
    }
    50% {
        transform: translateX(-1rem);
    }
    75% {
        transform: translateX(1rem);
    }
    100% {
        transform: translateX(0);
    }
}

.appear {
    animation: appear 0.2s ease normal;
}

.from-bottom {
    animation: from-left 0.7s normal;
}

.from-top {
    animation: from-right 0.7s normal;
}

@media(min-width: 768px) {
    .from-bottom {
        animation: from-bottom 0.7s normal;
    }
    .from-top {
        animation: from-top 0.7s normal;
    }    
}

.glass-image {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 128px;
    border-radius: 50%;
    height: 128px;
    z-index: 0;
    opacity: 1;
}
